body {
  margin-top: 80px;
  padding: 30px;
}
header {
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  display: flex;
  align-items: center;
  box-shadow: 0 0 5px 0 black;
}
header * {
  display: inline;
}

header li {
  margin: 20px;
}
header li a {
  color: black;
  text-decoration: none;
}
